html,
body {
  width: 100%;
  min-width: 320px;
  background: #000;
  color: #fff;
  overflow-x: hidden;
}
body {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
}
a {
  text-decoration: none;
}
a,
a:link,
a:visited,
a:hover,
a:active,
a:focus {
  transition: background-color 0.15s linear, background-position 0.15s linear, border 0.15s linear, box-shadow 0.15s linear, color 0.15s linear, opacity 0.15s linear, text-decoration 0.15s linear;
}
button,
input[type="submit"] {
  background: none;
  border: none;
  border-radius: 0;
}
figure {
  margin: 0;
}
blockquote {
  margin: 0;
  padding: 0;
}
.fs-grid .fs-row {
  max-width: 1300px;
}
.page_wrapper {
  position: relative;
}
.page_container {
  padding: 40px 0;
}
@media only screen and (min-width: 980px ) {
  .page_container {
    padding: 60px 0;
  }
}
.no-svg .has_svg {
  display: none !important;
}
.svg .no_svg {
  display: none !important;
}
.responsive_image img {
  display: block;
  width: 100%;
}
.video_wrapper {
  position: relative;
  height: 0px;
  width: 100%;
  padding-bottom: 56.25%;
}
.video_wrapper iframe,
.video_wrapper embed {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.bg_white + .bg_white {
  padding-top: 0 !important;
}
[data-checkpoint-animation=fade-right],
[data-checkpoint-animation=fade-up] {
  transition: opacity 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94), transform 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.link_arrow {
  font-family: "Open Sans Condensed", sans-serif;
  font-style: normal;
  font-weight: 700;
  display: inline-block;
  color: #000;
  font-size: 14px;
  letter-spacing: 0.25em;
  line-height: 24px;
  padding: 5px 0;
  text-transform: uppercase;
}
.link_arrow,
.link_arrow:link,
.link_arrow:visited,
.link_arrow:hover,
.link_arrow:active,
.link_arrow:focus {
  transition: background-color 0.2s ease, background-position 0.2s ease, border 0.2s ease, box-shadow 0.2s ease, color 0.2s ease, opacity 0.2s ease, text-decoration 0.2s ease;
}
.link_arrow:after {
  display: inline-block;
  font-size: 8px;
  margin: 0 0 1px 5px;
  transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  vertical-align: bottom;
}
@media only screen and (min-width: 740px ) {
  .link_arrow:after {
    font-size: 9px;
    margin-bottom: 5px;
  }
}
.link_arrow:hover,
a:hover .link_arrow,
.link_arrow:focus,
a:focus .link_arrow {
  color: #FF0000;
}
.link_arrow:hover:after,
a:hover .link_arrow:after,
.link_arrow:focus:after,
a:focus .link_arrow:after {
  transform: translateX(5px);
}
.button_arrow {
  font-family: "Open Sans Condensed", sans-serif;
  font-style: normal;
  font-weight: 700;
  display: inline-block;
  font-size: 14px;
  letter-spacing: 0.25em;
  line-height: 24px;
  padding: 5px 0;
  text-transform: uppercase;
  background: #FF0000;
  background-image: linear-gradient(to right, #fff 0, #fff 50%, #FF0000 50%, #FF0000 100%);
  background-position: 100% center;
  background-size: 200% 100%;
  border: 2px solid #FF0000;
  color: #000;
  padding: 13px 23px;
}
.button_arrow,
.button_arrow:link,
.button_arrow:visited,
.button_arrow:hover,
.button_arrow:active,
.button_arrow:focus {
  transition: background-color 0.2s ease, background-position 0.2s ease, border 0.2s ease, box-shadow 0.2s ease, color 0.2s ease, opacity 0.2s ease, text-decoration 0.2s ease;
}
.button_arrow:after {
  display: inline-block;
  font-size: 8px;
  margin: 0 0 1px 5px;
  transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  vertical-align: bottom;
}
@media only screen and (min-width: 740px ) {
  .button_arrow:after {
    font-size: 9px;
    margin-bottom: 5px;
  }
}
.button_arrow:hover,
a:hover .button_arrow,
.button_arrow:focus,
a:focus .button_arrow {
  color: #FF0000;
}
.button_arrow:hover:after,
a:hover .button_arrow:after,
.button_arrow:focus:after,
a:focus .button_arrow:after {
  transform: translateX(5px);
}
@media only screen and (min-width: 740px ) {
  .button_arrow {
    padding: 18px 33px;
  }
}
.button_arrow:hover,
a:hover .button_arrow,
.button_arrow:focus,
a:focus .button_arrow {
  background-position: 0% center;
  color: #FF0000;
}
.button_arrow_black {
  border-color: #000;
  background-image: linear-gradient(to right, #fff 0, #fff 50%, #000 50%, #000 100%);
  color: #FF0000;
}
.button_arrow_black:hover,
a.button_arrow_black:hover,
a:hover .button_arrow_black,
.button_arrow_black:focus,
a.button_arrow_black:focus,
a:focus .button_arrow_black {
  color: #000;
}
@media only screen and (min-width: 740px ) {
  .padded_item:nth-child(odd) {
