.header {
  position: fixed;
  display: flex;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  align-items: center;
  background: #000;
  color: #fff;
  justify-content: space-between;
  padding: 0 15px;
  transition: background 0.15s linear;
}
@media only screen and (min-width: 980px ) {
  .header {
    padding: 0 30px;
