.header_nav_handle {
  overflow: hidden;
  position: absolute;
  height: 40px;
  width: 40px;
  top: 15px;
  right: 15px;
  bottom: 15px;
  display: none;
  line-height: 0;
  margin: auto 0;
  padding: 0;
}
.header_nav_handle.fs-navigation-enabled {
  display: block;
}
.main_nav {
  text-align: center;
}
@media only screen and (max-width: 979px ) {
  .main_nav {
    display: none;
  }
}
.main_nav ul {
  float: right;
  list-style: none;
  margin: 0;
  padding: 0;
}
.main_nav li {
  position: relative;
  display: inline-block;
  margin: 0 10px;
}
.main_nav li a {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #fff;
  font-size: 12px;
  letter-spacing: 0.3em;
  padding: 28px 5px;
  text-transform: uppercase;
}
.main_nav li a:hover {
  color: #FF0000;
}
@media only screen and (min-width: 740px ) {
  .main_nav li a {
    padding: 28px 5px;
  }
}
.main_nav li:hover .sub-menu {
  opacity: 1;
  visibility: visible;
}
.main_nav li.menu-item-has-children > a:after {
  position: absolute;
  display: block;
  height: 8px;
  width: 8px;
  right: 0;
  bottom: 10px;
  left: 0;
  border: 1px solid #FF0000;
  border-width: 0 2px 2px 0;
  content: '';
  margin: 0 auto;
  transform: rotate(45deg);
}
@media only screen and (min-width: 980px ) {
  .main_nav li.menu-item-has-children > a:after {
    height: 10px;
    width: 10px;
    bottom: 14px;
  }
}
.flipped .main_nav li a {
  color: #000;
}
.flipped .main_nav li a:hover {
  color: #FF0000;
}
.main_nav .sub-menu {
  position: absolute;
  width: 200px;
  top: 100%;
  left: 50%;
  background: #000;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.05);
  padding: 10px 0;
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.15s linear,
      visibility 0.15s linear;
  visibility: hidden;
}
.main_nav .sub-menu li {
  display: block;
}
.main_nav .sub-menu li a {
  color: #CCCCCC;
  padding: 5px 10px;
}
.main_nav .sub-menu li a:hover {
  color: #FF0000;
}
.main_nav .sub-menu li.menu-item-has-children > a:after {
  display: none;
}
.main_nav .sub-menu li.current-menu-item > a {
  color: #FF0000;
}
.flipped .main_nav .sub-menu {
  background: #fff;
}
.flipped .main_nav .sub-menu li a {
  color: #666666;
}
.flipped .main_nav .sub-menu li a:hover {
  color: #FF0000;
}
