.circle,
.marker {
  opacity: 0;
  transition: opacity 0.25s linear;
}
.fs-checkpoint-active .circle,
.fs-checkpoint-active .marker {
  opacity: 1;
}
.desktop .circle_2 {
  transition-delay: 0.7s;
}
.mobile .circle_2 {
